const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			Inv: 0,
			imgUrl: '',
			publishList: [],
			userInfo: []
		}
	},
	onLoad() {
		// 获取用户信息
		this.getUserInfo()
		// 获取我的发布
		this.getPublish()
		this.imgUrl = helper.imgUrl
	},
	methods: {
		changeTab(e) {
			console.log(e)
			this.Inv = e;
		},
		// 编辑
		edit() {
			uni.navigateTo({
				url: `/pages/meEdit`
			})
		},
		// 粉丝
		meFan() {
			uni.navigateTo({
				url: `/pages/meFan`
			})
		},
		// 关注
		meFollow() {
			uni.navigateTo({
				url: `/pages/meFollow`
			})
		},
		// 获取我的发布列表
		getPublish() {
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/publish`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					console.log(res);
					uni.hideLoading();
					res = helper.null2str(res)
					if (res.data.status_code == '1') {
						this.publishList = res.data.data
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						});
					}
				}
			});
		},
		getUserInfo() {
			// 用户信息获取
			uni.showLoading({
				title: '用户信息获取中...'
			});
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					// console.log(res,'++++++')
					console.log(res.data, '+++++++++')
					this.userInfo = res.data
					if (res.data.status_code == 200) {


					}

				}
			})
		}
	}
}
